// <---------- Variables For Navbar Module ---------->
@hover-fade-out-duration: 100ms;
@link-list-width: 160px;
@top-link-border-radius: 20px 20px 0 0;
@bottom-link-border-radius: 0 0 20px 20px;

// <---------- Colors For Navbar Module ---------->

@header-color: white;
@navbar-color: rgba(78, 78, 78, 0.253);
@nav-link-color: white;
@rainbow-color: linear-gradient(
  45deg,
  #ff0000,
  #ff7300,
  #fffb00,
  #48ff00,
  #00ffd5,
  #002bff,
  #7a00ff,
  #ff00c8,
  #ff0000
);
@link-list-bg-color: rgba(0, 0, 0, 0.9);
@link-hover-bg-color: rgba(255, 255, 255, 0.2);
@profile-shadow-hover-color: rgba(255, 255, 255, 0.9);

:root {
  --link-text-color-dark: "#0861f1";
  
  --default-light-theme-color: rgba(255,255,255,0.9);
  --default-dark-theme-color: rgba(0,0,0,0.9);
  --box-shadow-and-border-color: yellowgreen;
  --theme-button-border-size: 2.5px;
  --navbarWidth-mobile: 600px;
  color-scheme:dark light;
}

// <---------- Styles For Navbar Module ---------->

.navbar {
  display: flex;
  justify-content: center;
  margin-top: 5px;
  z-index: 10;
}

.nav-container {
  align-items: center;
  background: @navbar-color;
  border-radius: 30px;
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  position: fixed;
  top: 10px;
  transition: background, 200ms ease-in-out;
  width: 98%;
  z-index: 10;
}

.header {
  align-items: center;
  color: @header-color;
  display: flex;
  font-family: @tangerine;
  font-size: 2em;
  justify-content: space-between;
  margin-left: 10px;
  margin-right: 10px;

  img {
    height: 25px;
    margin-left: 5px;
    width: 25px;
  }
}

.link-container {
  align-items: center;
  display: flex;
  flex-direction: row;
  list-style-type: none;
  margin-right: 100px;
  opacity: 1;
  outline: none;
  transition: opacity, 200ms ease-in-out;

  .link {
    .navbar-nested-link {
      background: none;
      border-radius: 10px;
      color: @nav-link-color;
      margin: 0 10px;
      outline: none;
      padding: 10px;
      text-decoration: none;
      transition: background, @hover-fade-out-duration ease-in-out;

      &:hover {
        background: @link-hover-bg-color;
        transition: background, @hover-fade-out-duration ease-in-out;
      }
    }
  }

  .link-4 {
    margin-right: 10px;
  }
}

.profile-container {
  align-items: center;
  display: flex;
  justify-content: space-around;
  list-style-type: none;
  position: absolute;
  right: 10px;
  top: 10px;
  transition: margin-right, 0.2s ease-in-out;

  .profile-img-link {
    border-radius: 50%;
    height: 40px;
    list-style-type: none;
    width: 40px;

    &:hover {
      box-shadow: 0 0 5px 2.5px @profile-shadow-hover-color;
      transition: box-shadow, 200ms ease-in-out;
    }
  }

  .profile-img {
    border-radius: 50%;
    height: 100%;
    object-fit: cover;
    width: 100%;
  }

  .theme-btn {
    border-radius: 50%;
    cursor: pointer;
    font-size: 33px;
    margin-bottom: 1px;
    margin-left: 5px;
    padding: 5px 5px 2.5px;
    position: absolute;
    right: 45px;
    text-shadow: 0 0 0 #fff;
    top: -2px;
    transition: text-shadow 50ms ease-in-out;

    &:hover {
      text-shadow: 0 0 10px #fff;
      transition: text-shadow 50ms ease-in-out;
    }
  }
}

.utilities-container {
  background: none;
  border-radius: 50%;
  cursor: pointer;
  display: none;
  font-size: 1.9em;
  margin: 0;
  padding: 2.5px 7px;
  position: absolute;
  right: 5px;
  transition: background 1s ease-in-out;

  &:hover {
    animation: hamburger-menu 20s linear infinite;
    background: @rainbow-color;
    background-clip: text;
    background-size: 400%;
    border: none;
    color: #fff;
    outline: none;
    -webkit-text-fill-color: transparent;
    z-index: -1;
  }
  i {
    color:white;
  }
}

// <---------- Animations For Navbar Module ---------->

@keyframes hamburger-menu {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

// <---------- Media Queries For Navbar Module --------->

@media only screen and (max-width:600px) {
  .link-container {
    align-items: center;
    border-radius: 20px;
    display: block;
    flex-direction: column;
    height: auto;
    justify-content: space-between;
    margin: 10px 0;
    position: absolute;
    right: 5px;
    top: 60px;
    transition: width 0.2s ease-in-out;
    width: @link-list-width;

    .link {
      border-radius: 0;
      margin: 0;
      text-align: center;
      width: 100%;

      .navbar-nested-link {
        display: inline-block;
        margin: 0;
        width: 100%;
      }
    }

    .link-1 {
      .navbar-nested-link {
        border-radius: @top-link-border-radius;
      }
    }

    .link-2 {
      .navbar-nested-link {
        border-radius: 0;
      }
    }

    .link-3 {
      .navbar-nested-link {
        border-radius: 0;
      }
    }

    .link-4 {
      .navbar-nested-link {
        border-radius: @bottom-link-border-radius;
        margin: 0;
      }
    }
  }

  .utilities-container {
    display: inline-block;
  }

  .profile-container {
    margin-right: 55px;
    right: 5px;
    transition: margin-right, 0.2s ease-in-out;
  }

  .no-display {
    height: 0;
    opacity: 0;
    position: absolute;
    right: -10px;
    transition: right opacity, 200ms ease-in-out;
    width: 0;
  }

  .remove-list {
    display: none;
  }
}
